.about {
  height: 100vh;
  background-color: #1e1e1e;
  &__title {
    text-transform: uppercase;
    text-align: center;
    position: relative;
    color: #fff;
    font-family: "Inter";
    padding-top: 100px;
    margin: 0;
    @media (min-width: 992px) {
      text-align: left;
      font-size: 40px;
    }
    &::after {
      content: "";
      position: absolute;
      bottom: -10px;
      left: 0;
      right: 0;
      height: 2px;
      background: linear-gradient(
        132.77deg,
        #e0d2ff 7.48%,
        #efc4fe 27%,
        #e9fffb 58.5%,
        #b7b2f2 92.67%
      );
      @media (min-width: 992px) {
        bottom: 10px;
        left: 250px;
      }
    }
  }
  &__text {
    color: #fff;
    font-family: "Inter";
    font-size: 20px;
    @media (min-width: 992px) {
      font-size: 30px;
    }
  }
}
